.recent-dropdown{
    padding: 30px;
    background-color: white;
    border-radius: 5px;
}

.recent-dropdown-top {
    display: grid;
    grid-template-columns: 1fr auto;
    align-items: center;
    padding-bottom: 20px;
}

.recent-dropdown-top h2{
    font-size: 1rem;
    font-weight: bold;
    color: #171d41;
}

.recent-dropdown-top .view-all-btn , .action-open-btn{
    color: #171d41;
    font-size: 0.9rem;
    background-color: #ebebeb;
    padding: 6px 15px;
    border-radius: 5px;
    cursor: pointer;
}

.recent-actions-dropdown{
    margin-top: 20px;
    display: grid;
    grid-template-columns: auto 1fr auto;
    justify-content: space-between;
    align-items: center;
}

.action-icon svg{
    width:2rem;
    height: 1.5rem;
    color: #545563;
    margin: auto;
    vertical-align: bottom;
}

.action-icon{
    background: #d2d5de;
    width: 50px;
    height: 50px;
    display: grid;
    place-items: center;
    border-radius: 50px;
    margin-right: 20px;
}

.action-main-content h5{
    color: #171d41;
    font-size: 14px;
}

.action-main-content h5 span {
    font-weight: bold;
}

.action-main-content p{
    color: #a5adbd;
    font-size: 13px;
}

.action-time p{
    color: #697691;
    font-size: 12px;
}