.menu-wrapper {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    padding: 2px 1rem;
    border-bottom: 1px solid #9e9e9e4a;
    background-color: white;
    position: fixed;
}

.hamburger-menu {
    padding: 8px;
    margin-right: 10px;
    margin-left: -10px;
}

.hamburger-menu:hover, .hamburger-click {
    background-color: #F1F2F4;
    display: grid;
    place-content: center;
    padding: 8px;
    margin-right: 10px;
    margin-left: -10px;
    cursor: pointer;
    border-radius: 5px;
}

.left-menus, .right-menus {
    display: flex;
}

.left-menus {
    width: 60%;
}

.right-menus {
    width: 40%;
    justify-content: flex-end;
    align-items: center;
}

.right-menus>div {
    margin-left: 1.5rem;
}

.logo img {
    width: 50%;
}

.logo-menu, .menus {
    display: flex;
    justify-content: center;
    align-items: center;
}

.left-menu-item {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #4D5165;
    cursor: pointer;
    position: relative;
}

.left-menu-item .menu-item {
    display: flex;
    padding: 8px;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
}

.left-menu-item .menu-item:hover , .menu-item-click {
    background-color: #F1F2F4;
}

.left-menu-item h4 {
    color: #4D5165;
    font-weight: bold;
}

.left-menu-item svg {
    width: 2rem;
    height: 1.2rem;
    margin-left: -8px;
}

.logo-menu svg {
    height: 1.5rem;
    width: 1.5rem;
    color: #171D41;
    /* margin-right: 10px; */
}

.left-menu-item {
    margin-left: 2rem;
}

.btn {
    display: flex;
    align-items: center;
    background-color: #171D41;
    padding: 10px;
    color: white;
    border-radius: 5px;
}

.btn-add svg {
    height: 1.5rem;
    width: 2rem;
}

.search-wrapper svg, .notification-wrapper svg {
    height: 1.6rem;
    width: 2rem;
    vertical-align: bottom;
}

.notification-wrapper {
    position: relative;
}

.badge {
    width: 10px;
    height: 10px;
    background-color: red;
    border-radius: 50px;
    position: absolute;
    top: 0;
    margin-right: 2px;
    right: 0;
}

.avatar {
    background-color: #F6F6F6;
    width: 45px;
    height: 45px;
    display: grid;
    place-items: center;
    border-radius: 5px;
    border: 2px solid #E9E9E9;
    cursor: pointer;
}

.avatar h4 {
    font-size: 20px;
    color: #4D5165;
}

.avatar-dropdown {
    position: absolute;
    background-color: white;
    width: 17rem;
    right: 0;
    margin-top: 5px;
    border-radius: 10px;
    margin-right: 20px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
}

.avatar-dropdown-blue {
    background-color: #171D41;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px;
}

.avatar-image {
    background-color: #353E72;
    width: 70px;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    position: relative;
}

.avatar-status {
    width: 13px;
    height: 13px;
    background-color: #39FF71;
    border-radius: 50px;
    position: absolute;
    bottom: 0;
    right: 0;
    margin-bottom: 10px;
}

.avatar-dropdown-blue .name {
    font-size: 0.7rem;
    margin-top: 10px;
}

.rating {
    display: flex;
    justify-content: center;
    align-items: center;
}

.rating-star svg {
    vertical-align: bottom;
}

.rating p {
    margin-top: 8px;
    margin-left: 10px;
    font-size: 13px;
    color: #697691;
}

.my-account, .settings, .logout {
    display: flex;
    align-items: center;
    padding: 15px 25px;
    cursor: pointer;
}

.my-account:hover, .settings:hover, .logout:hover {
    background-color: #f1f1f1;
}

.my-account {
    margin-top: 15px;
}

.settings {
    margin-bottom: 15px;
}

.logout {
    margin-top: 15px;
    margin-bottom: 20px;
}

.my-account h4, .settings h4, .logout h4 {
    margin-left: 10px;
    font-size: 13px;
    color: #5C677F;
}

.line {
    width: 100%;
    border: 0.5px solid #6363646d;
}

.my-account svg, .settings svg, .logout svg {
    height: 1.4rem;
    width: 2rem;
    color: #5C677F;
}

.recent-dropdown-wrapper, .help-dropdow-wrapper {
    position: absolute;
    margin-top: 45px;
    width: 35rem;
    top: 0;
    border-radius: 5px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
}

.help-dropdow-wrapper {
    width: 30rem;
}