.d-none {
    display: none;
}

.status-display {
    display: flex;
    justify-content: center;
    align-items: center;
}

.status-display * {
    margin-right: 5px;
}

.status-display svg {
    margin-bottom: 5px;
}

.statuses svg {
    height: 1.5rem;
    margin-top: 5px;
}

.dropdown {
    display: flex;
    border: 2px solid #E9E9E9;
    padding: 5px;
    border-radius: 10px;
    align-items: center;
    justify-content: space-around;
    width: 10rem;
    height: 45px;
    background-color: white;
}

.dropdown svg {
    margin-top: auto;
    margin-bottom: auto;
    height: 1rem;
    width: 1rem;
    stroke-width: 1;
    vertical-align: bottom;
}

.status-message p, h5 {
    color: #4D5165;
}

.dropdown-select {
    position: absolute;
    width: 10rem;
    border-radius: 5px;
    margin-top: 5px;
    background-color: white;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
}

.statuses {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 3px;
    padding: 0 10px;
    cursor: pointer;
}

.statuses svg{
    margin-top: 5px;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1);
}

.statuses:last-child {
    margin-bottom: 10px;
}

.statuses:first-child {
    margin-top: 10px;
}

.showedit{
    display: block;
}

.hideedit {
    visibility: hidden;
}

.statuses:hover {
    background-color: #f1f1f1;
}