.sidebar-wrapper {
  display: flex;
  flex-direction: row;
  background-color: transparent;
  min-width: 2em;
  width: fit-content;
  font-size: 1rem;
  position: fixed;
  margin-top: 55px;
}

.sidebar {
  background-color: #171c40;
  color: #fff;
  min-height: 100vh;
  height: max-content;
  min-width: 55px;
  width: 100%;
  padding-top: 3%;
  display: flex;
  flex-direction: column;
  justify-content: start;
}
.sidebarActivated {
  display: block;
  display: flex;
  width: 200px;
  text-align: start;
  justify-content: flex-start;
  margin-left: 1%;
  align-items: flex-start;
}
.mainDiv {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
}
.sidebarIcon {
  margin-left: 5%;
  margin-top: 4%;
  margin-bottom: 4%;
  background-color: #fff;
  width: 40px;
  height: 40px;
  border-radius: 4px;
  opacity: 0.2;
}

.popupDiv {
  display: flex;
  width: 80%;
  justify-content: space-around;
  align-items: flex-start;
  display: none;
  text-align: start;
}
.popupDiv p {
  margin-left: 10px;
}
#BsChevronRight {
  display: none;
}
.popupDivActive {
  display: block;
  display: flex;
  width: 200px;
  text-align: start;
  justify-content: flex-start;
  margin-left: 1%;
  align-items: center;
}

.sidebarExpand {
  background-color: #171c40;
  color: #fff;
  min-height: 100vh;
  height: max-content;
  min-width: 300px;
  width: 100%;
  padding-top: 3%;
  display: flex;
  flex-direction: column;
  justify-content: start;
  margin-left: 0;
}
.sidebarExpand .mainDiv {
  margin: 0;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
}

.sidebarExpand .popupDiv {
  display: block;
  color: #fff;
}

.sidebarExpand #BsChevronRight {
  color: #fff;
  display: flex;
  position: absolute;
  left: 14rem;
}
.sidebarExpand .sidebarIcon {
  margin-left: 4%;
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 0;
  padding: 0;
}
.serviceDeskExpand {
  background-color: #263166;
  color: #fff;
  width: max-content;
  height: max-content;
  position: fixed;
  top: 130px;
  left: 300px;
  padding-top: 1em;
  padding-bottom: 1em;
}

.serviceDeskExpandDeactivated {
  display: none;
}

.serviceDeskExpand ul {
  list-style: none;
  text-align: start;
  margin: 0;
  padding: 0;
}
.serviceDeskExpand li:hover {
  background-color: #111434;
}

.serviceDeskExpand p {
  margin-left: 1em;
  margin-right: 1em;
  padding: 0.5em;
  margin: 0;
}

/* HOVER */

.sidebar:hover {
  width: 300px;
}

.sidebar:hover .popupDiv {
  display: block;
  display: flex;
  text-align: start;
  justify-content: space-between;
  align-items: flex-start;
}

.sidebar:hover .sidebarIcon {
  margin-left: 4%;
  margin-top: 0;
  margin-bottom: 0;
}

.sidebar:hover #BsChevronRight {
  display: flex;
  position: absolute;
  left: 14rem;
}

.mainDiv:hover {
  background-color: #111434;
}
