
.bookmarks-dropdown {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.vertical-line {
    width: 1px;
    background-color: #DEDEDE;
    height: 50px;
}

.bookmarks-dropdown-top {
    display: grid;
    grid-template-columns: auto 1fr auto;
    align-items: center;
    padding-bottom: 20px;
}

.bookmarks-dropdown-top .select-timeline {
    position: relative;
    margin-left: 10px;
}

.select-timeline input {
    padding: 5px;
    border: 2px solid #e9e9e9;
    border-radius: 5px;
    color: #a5adbd;
    cursor: pointer;
}

.select-timeline .clock-icon svg {
    color: #e9e9e9;
}

.select-timeline .clock-icon {
    position: absolute;
    top: 0;
    left: 0;
    margin-left: 120px;
    margin-top: 7px;
}

.select-time ul {
    list-style-type: none;
}

.select-time {
    position: absolute;
    background-color: white;
    border-radius: 5px;
    width: auto;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
}

.select-time ul li {
    font-size: 14.5px;
    padding: 5px 10px;
}

.select-time ul li:first-child {
    margin-top: 10px;
}

.select-time ul li:last-child {
    margin-bottom: 10px;
}

.select-time ul li:hover {
    background-color: #f5f4f4;
}