.help-dropdown {
    padding: 30px;
    background-color: white;
    border-radius: 5px;
}

.help-dropdown h4 {
    font-weight: bold;
    font-size: 1rem;
    color: #171d41;
}

.help-search-wrapper {
    margin: 20px 0px;
    position: relative;
}

.help-search-wrapper input {
    width: 100%;
    padding: 15px;
    border: 0px;
    border-radius: 5px;
    background-color: #f1f1f1;
}

.help-search-wrapper svg {
    position: absolute;
    right: 0;
    margin-top: 13px;
    margin-bottom: 10px;
    margin-right: 8px;
    width: 2rem;
    height: 1.5rem;
    color: #858692;
}

.help-card{
    display: grid;
    grid-template-columns: auto 1fr auto;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #EDEDED;
    padding: 12px;
    border-radius: 5px;
    margin-top: 10px;
}

.help-card:hover{
    background-color: #F1F1F1;
}

.help-card-arrow{
    width: 25px;
    height: 25px;
    /* background-color: #171d41; */
    display: grid;
    place-items: center;
    border-radius: 50px;
    border: 1px solid #BEBEBE;
}

.help-card svg{
    vertical-align: center;
    width: 1.5rem;
    height: 1rem;
    color: #BEBEBE;
}

.help-card .help-card-img {
    width: 50px;
    height: 50px;
    background-color: #E8E8E8;
    border-radius: 5px;
    margin-right: 15px;
}

.help-card-content p {
    font-size: 13px;
    margin-top: 3px;
    color: #7F8086;
}